<template>
<div class="box">
    <Header>
      <div slot="center">用户地址编辑</div>
    </Header>
    <div class="content">
       <van-address-edit
        :area-list="areaList"
        show-postal
        show-delete
        show-set-default
        show-search-result
        :search-result="searchResult"
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </div>
  </div>

</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'
import city from '@/utils/city'
import Vue from 'vue'
import { AddressEdit, Toast } from 'vant'
Vue.use(AddressEdit)
Vue.use(Toast)
export default {
  components: {
    Header
  },
  data () {
    return {
      areaList: city,
      searchResult: [],
      selectArea: []
    }
  },

  methods: {
    onSave (content) {
      console.log(content)
      axios.post('/address/add', {
        userid: localStorage.getItem('userid'),
        name: content.name,
        tel: content.tel,
        province: content.province,
        city: content.city,
        county: content.county,
        addressDetail: content.addressDetail,
        flag: content.isDefault,
        areaCode: content.areaCode,
        postalCode: content.postalCode
      }).then(() => {
        Toast('添加地址成功')
        this.$router.back()
      })
    },
    onDelete () {
      Toast('delete')
    },
    onChangeDetail (val) {
      if (val) {
        let str = ''
        this.selectArea.map(item => {
          str += item.name + ' '
        })
        this.searchResult = [{
          name: val,
          address: str
        }]
      } else {
        this.searchResult = []
      }
    }
  }
}
</script>
